import React from 'react';
import './Education.css';
import { EducationTimeSeries } from '../../components/EducationTimeSeries';
import Zoom from 'react-reveal/Zoom';
var config = require('../../config.json')

export function Education(){
    const educationData = config.Education
    return <div className="Education" id="Education"> 
        <div className="Heading">
            <h2> Education </h2>
        </div>
            <div>
                {
                    educationData.map((value) => {
                        return(
                            <div> 
                                <Zoom duration={1000}>
                                    <EducationTimeSeries data={value}/> 
                                </Zoom>
                            </div>
                        )
                    })
                }
            </div>
    </div>
}