import React, {useState, useEffect} from 'react';
import {AppBar, Toolbar, Typography, Button, makeStyles, IconButton, Drawer, Link, MenuItem} from "@material-ui/core";
import MenuIcon from '@material-ui/icons/Menu';
var config = require('../../config.json')

const useStyles = makeStyles(() => ({
    header: {
      backgroundColor: "#eef1f4",
    },
    name: {
      fontWeight: 500,
      color: "#797676",
      textAlign: "left",
    },
    headerOptions: {
        fontWeight: 700,
        size: "18px",
        textAlign: "right",
        marginLeft: "38px",
        color: "#797676",
        underline: "hover"
    },
    menuOptions: {
        fontWeight: 500,
        size: "18px",
        textAlign: "center",
        color: "#797676",
        padding: "3vw"
    },
    drawer: {
        background: "#eef1f4"
    },
    toolbar: {
        display: "flex",
        justifyContent: "space-between",
    },
}));

export function Header(){
    const [state, setState] = useState({
        mobileView: false,
        menuOpen: false
    })

    const { mobileView, menuOpen } = state;

    useEffect(() => {
        const setResponsiveness = () => {
            return window.innerWidth < 900
                ? setState((prevState) => ({...prevState, mobileView : true}))
                : setState((prevState) => ({...prevState, mobileView: false}))
        };
        setResponsiveness();
        window.addEventListener("resize", () => setResponsiveness());
    }, []);

    const classes = useStyles();
    const headerOptionsList = config.Header

    const getName = () => {
        return (
            <Typography variant="h5" component="h1" className={classes.name}>
            Eshita Arza
            </Typography>
        )
    };

    const getHeaderOptions = () => {
        return headerOptionsList.map(({label, href}) => {
            return (
                <Link 
                    className={classes.headerOptions}
                    href={href}
                >
                    {label}
                </Link>
            )
        })
    };

    const getMenuOptions = () => {
        return headerOptionsList.map(({label, href}) => {
            return (
                <MenuItem> 
                    <Link 
                        className={classes.menuOptions}
                        href={href}
                    >
                        {label}
                    </Link>
                </MenuItem>
            )
        })
    };

    const headerDesktopView = () => {
        return <Toolbar className={classes.toolbar}> 
        {getName()}
        <div>  
            {getHeaderOptions()}
        </div>
        </Toolbar>;
    };

    const headerMobileView = () => {
        //When the menu open function is called we set menuOpen state to true
        const handleMenuOpen = () => {
            setState((prevState) => ({ ...prevState, menuOpen:true }));
        }

        //When the user clicks anywhere else on the screen we set menuOpen state to false and the menu is closed
        const handleMenuClose = () => {
            setState((prevState) => ({ ...prevState, menuOpen:false }));
        }

        return <Toolbar className={classes.toolbar}> 
            <IconButton 
                {...{
                    edge: "start",
                    onClick: handleMenuOpen,
                }}
            >
                <MenuIcon /> 
            </IconButton>
            <Drawer
                {...{
                    anchor: "left",
                    open: menuOpen,
                    onClose: handleMenuClose,
                    className: "drawer"
                }}
            >
                <div>{getMenuOptions()}</div>
            </Drawer>
            {/* <div>   */}
                {getName()}
            {/* </div> */}
        </Toolbar>;
    };

    return(
        <header>
            <AppBar className={classes.header}> {mobileView ? headerMobileView() : headerDesktopView()} </AppBar>
        </header>
    )
}