import React, {useState, useEffect} from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Grid from '@material-ui/core/Grid';
import potrait from '../../assets/logo.png';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import IconButton from '@material-ui/core/IconButton';
import InstagramIcon from '@material-ui/icons/Instagram';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import GitHubIcon from '@material-ui/icons/GitHub';
var config = require('../../config.json');

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        width: '100%',
        height: '100%',
        justifyContent: 'center'
    },
    details: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        minWidth: '25vw'
    },
    title: {
        fontWeight: 700,
        color: '#797676',
        textAlign: 'left'
    },
    body: {
        fontWeight: 500,
        color: '#797676',
        textAlign: 'left',
        paddingTop: "7vh",
        paddingBottom: "7vh",
        justifyContent: 'center'
    },
    cover: {
        width: '50%'
    },
    iconsRoot: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        fontSize: '2rem'
    },
    icons: {
        display: 'flex',
        alignItems: 'fled-end',
        justify: 'space-evenly',
        justifyContent: 'center'
    },
    icon:{
        fontSize: '2rem'
    },
    mobileRoot: {
        display: 'flex',
        width: '100%',
        height: '70%',
        justifyContent: 'center'
    },
    mobileBody: {
        fontWeight: 500,
        color: '#797676',
        textAlign: 'left',
        paddingTop: "3vh",
        paddingBottom: "3vh",
        justifyContent: 'center'
    },
    mobileIcon:{
        fontSize: '1rem'
    }
}));

export function HomeCard() {
    const classes = useStyles();
    const homeData = config.Home

    const [state, setState] = useState({
        mobileView: false
    })

    const { mobileView } = state;

    useEffect(() => {
        const setResponsiveness = () => {
            return window.innerWidth < 900
                ? setState((prevState) => ({...prevState, mobileView : true}))
                : setState((prevState) => ({...prevState, mobileView: false}))
        };
        setResponsiveness();
        window.addEventListener("resize", () => setResponsiveness());
    }, []);

    const desktopLayout = () => {
        return (
            <Grid container>
                <Grid item>
                    <Card classes={{root : classes.root}} variant="outlined">
                        <CardMedia
                            className={classes.cover}
                            image={potrait}
                            title="Potrait"
                        />
                        <div className={classes.details}>
                            <CardContent>
                                <Typography variant="h3" component="h1" className={classes.title}>{homeData.name}</Typography>
                                <Typography variant="subtitle1" className={classes.title}>{homeData.title}</Typography>
                                <div>
                                    <Typography variant="h5" className={classes.body}>
                                        {homeData.content}
                                    </Typography>
                                </div>
                            </CardContent>
                        </div>
                    </Card>
                    <Card classes={{root : classes.iconsRoot}} variant="outlined">
                        <div className={classes.icons}>
                            <IconButton href={homeData.linkedin}>
                                <LinkedInIcon classes={{root: classes.icon}}/>
                            </IconButton>
                            <IconButton href={homeData.github}>
                                <GitHubIcon classes={{root: classes.icon}}/>
                            </IconButton>
                            <IconButton href={homeData.email}>
                                <MailOutlineIcon classes={{root: classes.icon}}/>
                            </IconButton>
                            {/* <IconButton>
                                <InstagramIcon/>
                            </IconButton> */}
                        </div>  
                    </Card>
                </Grid>
            </Grid>
        );
    }

    const mobileLayout = () => {
        return (
            <Grid container>
                <Grid item>
                    <Card classes={{root : classes.mobileRoot}} variant="outlined">
                        <CardMedia
                            className={classes.cover}
                            image={potrait}
                            title="Potrait"
                        />
                    {/* </Card>
                    <Card classes={{root : classes.mobileRoot}} variant="outlined"> */}
                        <div className={classes.details}>
                            <CardContent>
                                <Typography variant="h5" component="h1" className={classes.title}>{homeData.name}</Typography>
                                <Typography variant="subtitle1" className={classes.title}>{homeData.title}</Typography>
                                <div>
                                    <Typography className={classes.mobileBody}>
                                        {homeData.content}
                                    </Typography>
                                </div>
                            </CardContent>
                        </div>
                    </Card>
                    <Card classes={{root : classes.iconsRoot}} variant="outlined">
                        <div className={classes.icons}>
                            <IconButton href={homeData.linkedin}>
                                <LinkedInIcon classes={{root: classes.mobileIcon}}/>
                            </IconButton>
                            <IconButton href={homeData.github}>
                                <GitHubIcon classes={{root: classes.mobileIcon}}/>
                            </IconButton>
                            <IconButton href={homeData.email}>
                                <MailOutlineIcon classes={{root: classes.mobileIcon}}/>
                            </IconButton>
                            {/* <IconButton>
                                <InstagramIcon/>
                            </IconButton> */}
                        </div>  
                    </Card>
                </Grid>
            </Grid>
        );
    }

    return (mobileView ? mobileLayout() : desktopLayout())
}