import React from 'react';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import Typography from '@material-ui/core/Typography';
import {makeStyles} from '@material-ui/core/styles';
import SchoolRoundedIcon from '@material-ui/icons/SchoolRounded';
import SchoolIcon from '@material-ui/icons/School';
import Paper from '@material-ui/core/Paper';
import Fade from '@material-ui/core/Fade';

const useStyles = makeStyles((theme) => ({
    paper: {
      padding: '6px 16px',
      color: theme.palette.text.secondary,
      boxShadow: "none"
    },
    secondaryTail: {
      backgroundColor: theme.palette.secondary.main,
    },
    title: {
        fontWeight: 700,
        size: "30px",
    },
    body: {
        size: "18px",
    },
    time: {
        fontWeight: 700,
        size: "18px",
    }
}));


export function EducationTimeSeries(props) {
    const classes = useStyles();
    return (
        <TimelineItem>
            <TimelineOppositeContent>
                <Typography className={classes.time}> {props.data.datePeriod} </Typography>
            </TimelineOppositeContent>
            <TimelineSeparator>
                <TimelineDot>
                {props.data.education === true ? <SchoolRoundedIcon /> : <></>}
                </TimelineDot> 
                <TimelineConnector />
            </TimelineSeparator>
            <TimelineContent>
            <Paper elevation={3} className={classes.paper}>
                <Typography className={classes.title}> {props.data.designation}</Typography>
                <Typography className={classes.body}>{props.data.companyName}</Typography>
                <Typography className={classes.title}>{props.data.description}</Typography>
            </Paper>
            </TimelineContent>
        </TimelineItem>
    )
}