import React from 'react';
import './Experience.css';
import Timeline from '@material-ui/lab/Timeline';
import Zoom from 'react-reveal/Zoom';
import { ContentPaper } from '../../components/ContentPaper';
import { ExperienceTimeSeries } from '../../components/ExperienceTimeSeries';
var config = require('../../config.json')

export function Experience(){
    const experienceData = config.Experience
    return <div className="Experience" id="Experience"> 
        <div>
            <h2> Experience </h2>
        </div>
        {/* <Timeline align="alternate"> */}
            {
                experienceData.map((value) => {
                    return <div> 
                            <Zoom duration={1000}>
                                <ExperienceTimeSeries data={value}/> 
                            </Zoom>
                        </div>
                })
            }
        {/* </Timeline> */}
    </div>
}