import React from 'react';
import {makeStyles} from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import DescriptionIcon from '@material-ui/icons/Description';
import DescriptionOutlinedIcon from '@material-ui/icons/DescriptionOutlined';
import IconButton from '@material-ui/core/IconButton';
import Grid from '@material-ui/core/Grid';
import Divider from '@material-ui/core/Divider';
import Zoom from 'react-reveal/Zoom';

const useStyles = makeStyles((theme) => ({
    root: {
      flexGrow: 1,
      alignItems: "baseline",
      direction: "row"
    },
    card: {
      color: theme.palette.text.secondary
    },
    title: {
        fontWeight: 700,
        size: "20px",
        color: theme.palette.text.secondary
    },
    body: {
        size: "18px",
        color: theme.palette.text.secondary
    },
    icon:{
        fontSize: "2rem"
    },
    button:{
        paddingTop: "4%",
        paddingLeft: "80%"
    }
}));

export function ContentCard(props) {
    const classes = useStyles();
    const data = props.data

    const button = (link) => {
        return (
            <div className={classes.button}>
                <IconButton href={link}>
                    <DescriptionOutlinedIcon classes={{root: classes.icon}}/>
                </IconButton>
            </div>
        );
    }

    return (
        <Grid container alignItems="baseline" justify="space-evenly" spacing={4} direction="row" >
            {
                data.map(project => {
                    return (
                        <Grid item xs={12} sm={6} md={3}>
                            <Zoom duration={1000}>
                            <Card className={classes.card}>
                                <CardHeader 
                                    disableTypography="true" 
                                    title = {
                                        <Typography className={classes.title}>{project.title}</Typography>
                                    } 

                                    subheader = {
                                        <Typography className={classes.title}>{project.location}</Typography>
                                    }
                                />
                                <CardContent>
                                    <Typography className={classes.body}>
                                        {project.content}
                                    </Typography>
                                    {project.link == "" ? <></> : button(project.link)}
                                </CardContent>
                            </Card>
                            </Zoom>
                        </Grid>
                    )
                })
            }
        </Grid>
    );
}